@import "../variables";

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  @media (max-width: 645px) {
    padding: 20px 0;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 730px) {
      flex-direction: column;

      .header__cart {
        margin-top: 8px;
      }
    }
  }

  &__logo {
    display: flex;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;

      @media (max-width: 1060px) {
        display: none;
      }
    }

    @media (max-width: 730px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      img {
        margin-right: 0px;
        padding-bottom: 5px;
      }

      h1 {
        text-align: center;
      }
    }
  }
}
